import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DashboardService } from 'src/app/Services/dashboard.service';
declare var $;

@Component({
  selector: 'app-user-dashboard',
  templateUrl: './user-dashboard.component.html',
  styleUrls: ['./user-dashboard.component.css']
})
export class UserDashboardComponent implements OnInit {

  p: number = 1;
  paginate={ 
             itemsPerPage: 10,
             currentPage: 1,
             totalItems: 40
  } 

  editPostDetails:TableData;
  tableData:any;

  constructor(private router:Router,private dashBoardService:DashboardService) { }
  
  ngOnInit() {    
    this.dashBoardService.getScappedAtricles(this.p).subscribe(res=>{
      this.tableData=res;
    });
  }

  logOutUserSession(){
    localStorage.removeItem('UserDetails');
    this.router.navigate(['\login']);
  }
  editPost(item){
   // alert('Hi');
   this.editPostDetails=item;
   console.log(this.editPostDetails);
    $('#editModal').modal('show');
  }
  deletePost(){
    console.log(this.editPostDetails);
    $('#deleteModal').modal('show');
  }

  pageChanged(event){
    console.log(event);
    this.p=event;
    this.paginate.currentPage=this.p;
    this.dashBoardService.getScappedAtricles(this.p).subscribe(res=>{
      this.tableData=[];
      this.tableData=res;
      console.log(this.tableData);
    });
  }

}

//http://michaelbromley.github.io/ngx-pagination/#/

export interface TableData{
  title:string,
  source:string,
  author:string,
  image:string,
  organisation:string
  content:string
}
